import React, { FC } from "react";
import styled from "styled-components";
const Button = styled.button`
  font-size: 28px;
  margin: 0px;
  background: none;
  cursor: pointer;
  position: relative;
  vertical-align: top;
  height: 100%;
  padding: 0px 8px;
  width: 35.41%;
  border: none;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  &:hover {
    background-color: #9696ac;
    border-radius: 1000px;
  }
`;

export interface IButtonProps {
  title: string;
  eventClick(id: string);
  idItem: string;
}

const FilledCartItemButton: FC<IButtonProps> = ({
  title,
  eventClick,
  idItem,
}) => {
  const click = () => {
    eventClick(idItem);
  };
  return <Button onClick={click}>{title}</Button>;
};

export default FilledCartItemButton;
