import React, { FC, PropsWithChildren } from "react";
import SwitcherItem from "./SwitcherItem/SwitcherItem";
import "./Switcher.scss";
export interface ISwitcherProps<T, V> {
  enum: T;
  enumRus: V;
  name: string;
  switcherItemEvent(title: string, type: string): void;
}

const Switcher = <T, V>(props: PropsWithChildren<ISwitcherProps<T, V>>) => {
  return (
    <div className="switcher">
      {Object.values(props.enum).map((el) => (
        <SwitcherItem
          event={props.switcherItemEvent}
          key={el}
          id={`${el}`}
          name={props.name}
          title={props.enumRus[el]}
        />
      ))}
    </div>
  );
};

export default Switcher;
