import React, { FC } from "react";
import "./ClicableDiv.scss";

export interface IClicableDivProps {
  name: string;
  clickEvent(name?: string): void;
}

const ClicableDiv: FC<IClicableDivProps> = ({ name, clickEvent }) => {
  const click = () => {
    clickEvent(name);
  };
  return (
    <div onClick={click} className="fovarite__item">
      {name}
    </div>
  );
};

export default ClicableDiv;
