import { ActionsUnion, createAction } from "./../actions-helpers";
import { Dispatch } from "redux";
import { UsersApi } from "./../../api/rest/users.api";
import * as userActions from "../users";
import * as errorAction from "../../store/errorMessage/errorMessage.actions";
import * as currentUserActions from "../app/";
export const START_LOADING = "[app] START_LOADING";
export const FINISH_LOADING = "[app] FINISH_LOADING";

export const Actions = {
  startLoading: () => createAction(START_LOADING),
  finishLoading: () => createAction(FINISH_LOADING),
};

export const Thunks = {
  autorization: (login: string, password: string) => {
    return (dispatch: Dispatch) => {
      dispatch(Actions.startLoading());
      UsersApi.getOrCreateUsers().then((result) => {
        dispatch(userActions.Actions.finishFetchingUsers(result));
      });
      UsersApi.checkUser(login, password)
        .then((result) => {
          dispatch(Actions.finishLoading());
          dispatch(currentUserActions.Actions.addUser(result));
        })
        .catch((e) => {
          dispatch(errorAction.Actions.errorOpen(e));
          dispatch(Actions.finishLoading());
        });
    };
  },
};

export type Actions = ActionsUnion<typeof Actions>;
