import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../../store";
import * as shopActions from "../../../store/app";
import { IProduct } from "../../../api/models";
import Category from "./category/Category";
import "./Shop.scss";

const Shop = () => {
  const dispatch = useDispatch();
  const items = useSelector<RootState, IProduct[]>(
    (state) => state.app.product
  );
  const [title, setTitle] = useState<Array<string>>([]);

  const titleCreate = () => {
    let set = items.map((item) => item.type);
    set = set.filter(onlyUnique);
    setTitle(set);
  };

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }
  useEffect(() => {
    dispatch(shopActions.Thunks.onFetchAllItems());
  }, []);
  useEffect(() => {
    titleCreate();
  }, [items]);
  return (
    <main className="shop">
      {title?.map((elem) => (
        <Category productArray={items} key={elem} title={elem} />
      ))}
    </main>
  );
};

export default Shop;
