import { createSelector } from "reselect";
import { ICart } from "../cart";
import { RootState } from "./../state.types";
import { IAppReducer } from "./app.reducer";
export const getApp = (state: RootState) => state.app;

export const selectGetCurrentUser = createSelector(
  getApp,
  (app: IAppReducer) => {
    return app.user;
  }
);

export const selectGetCityCart = createSelector(getApp, (app: IAppReducer) => {
  return app.pickupCity;
});

export const selectLocalStorageOrder = createSelector(
  getApp,
  (app: IAppReducer) => {
    const order: ICart[] = JSON.parse(
      localStorage.getItem(String(app.user?.id)) || "[]"
    );
    return order;
  }
);
