import React, { FC } from "react";
import styled from "styled-components";
export interface IStepIndicatorProp {
  title: string;
  id: string;
  currentStatus: string;
}

const InputIndicator = styled.button<IStepIndicatorProp>`
  width: 100%;
  border: none;
  background-color: ${(props) =>
    props.currentStatus === props.id ? "orange" : "transparent"};
  border-radius: ${(props) =>
    props.currentStatus === props.id ? "50px" : "none"};
  &:active,
  &:focus-visible {
    outline: none;
  }
  &:hover {
    cursor: pointer;
  }
`;

const StepIndicatorItem: FC<IStepIndicatorProp> = ({
  title,
  id,
  currentStatus,
}) => {
  return (
    <>
      <InputIndicator currentStatus={currentStatus} id={id}>
        {title}
      </InputIndicator>
    </>
  );
};

export default StepIndicatorItem;
