import React, { FC } from "react";
import "./SwitcherItem.scss";
export interface ISwitcherItemProps {
  name: string;
  id: string;
  title: string;
  event(title: string, type: string): void;
}

const SwitcherItem: FC<ISwitcherItemProps> = ({ name, id, title, event }) => {
  const click = (e) => {
    event(id, name);
  };
  return (
    <div className="switchItem">
      <input type="radio" name={name} id={id} />
      <label onClick={click} htmlFor={id}>
        {title}
      </label>
    </div>
  );
};

export default SwitcherItem;
