import React, { FC } from "react";
import { IDops } from "../../Product";
import classNames from "classnames";
import "./IngProductRight.scss";
import SvgIngSelected from "../../../../commonComponent/svg/SvgIngSelected";
import SvgIngNotSelected from "../../../../commonComponent/svg/SvgIngNotSelected";

export interface IIngProps extends IDops {
  clickEvent(title: string, status: boolean): void;
}

const IngProductRight: FC<IIngProps> = ({ title, isSelect, clickEvent }) => {
  const click = () => {
    clickEvent(title, !isSelect);
  };

  const ingClass = classNames(
    "ingItem",
    { ingItem___true: isSelect },
    { ingItem__false: !isSelect }
  );
  return (
    <div onClick={click} className={ingClass}>
      <div className="ingItem__title">{title}</div>
      {isSelect ? <SvgIngSelected /> : <SvgIngNotSelected />},
    </div>
  );
};

export default IngProductRight;
