import * as models from "../models/index";
import { ICity } from "../models/index";
import { IOrdered, ItemsOrdered } from "./../../api/models/orderedSlider.model";
import { IPromItem } from "../models/promotionItems.model";

export class SliderItemApi {
  static async getItemPromotion(): Promise<IPromItem[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const data = models.promotionItem;
        resolve(data);
      }, 1000);
    });
  }

  static async getItemOrdered(): Promise<IOrdered[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const data = ItemsOrdered;
        resolve(data);
      }, 1000);
    });
  }
}
