import React, { FC } from "react";
import classNames from "classnames";
import "./Sides.scss";
import { ISideModify } from "./../../Product";
import SvgItemSides from "../../../../commonComponent/svg/SvgItemSides";
export interface ISidesProps {
  sides: ISideModify[];
  changeSide(title: string, choose: boolean);
}

export interface ISidesItemProps {
  side: ISideModify;
  changeSide(title: string, choose: boolean);
}

const ItemSides: FC<ISidesItemProps> = ({ side, changeSide }) => {
  const click = () => {
    changeSide(side.name, !side.isChoose);
  };
  const sidesClass = classNames("side", { active: side.isChoose });
  return (
    <div className={sidesClass}>
      {side.isChoose && <SvgItemSides></SvgItemSides>}
      <div onClick={click} className="side-wrap">
        <img src={side.img} alt="" />
        <div className="title">{side.name}</div>
      </div>
      <div className="price">{side.price} ₽</div>
    </div>
  );
};

const Sides: FC<ISidesProps> = ({ sides, changeSide }) => {
  return (
    <div className="sides">
      <div className="sides__title">Добавить в пиццу</div>
      <div className="sides-wrap">
        {sides.map((el) => {
          return <ItemSides key={el.name} changeSide={changeSide} side={el} />;
        })}
      </div>
    </div>
  );
};

export default Sides;
