import React, { FC } from "react";
import { IProduct } from "../../../../api/models";
import CommonTitle from "../../../commonComponent/commonTitle/CommonTitle";
import ShopItem from "../../../commonComponent/shopItem/ShopItem";
import "./Category.scss";
import { TypeFoodRus } from "./../../../../api/enums/pizza-enum";

export interface ICategoryProps {
  title: string;
  productArray: Array<IProduct>;
}

const Category: FC<ICategoryProps> = ({ title, productArray }) => {
  return (
    <div className="category">
      <CommonTitle title={TypeFoodRus[title]}></CommonTitle>
      <div className="category-wrap">
        {productArray.map(
          (item) =>
            item.type === title && (
              <ShopItem key={item.id} item={item}></ShopItem>
            )
        )}
      </div>
    </div>
  );
};

export default Category;
