import { Type } from "typescript";
import * as shopItemModel from "../models/product.model";
import { IProduct } from "../models/product.model";

export class ShopItemApi {
  static async getPizza(): Promise<Array<IProduct>> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const data: Array<IProduct> = shopItemModel.Items;
        resolve(data);
      }, 500);
    });
  }
}
