import React, { FC } from "react";
import "./UserAvailable.scss";
export interface IUserAvailableProps {
  word: string;
  redirectToProfile(): void;
}

const UserAvailable: FC<IUserAvailableProps> = ({
  word,
  redirectToProfile,
}) => {
  return (
    <div
      data-testid="userLogged"
      onClick={redirectToProfile}
      className="account-empty"
    >
      {`User: ${word}`}
    </div>
  );
};

export default UserAvailable;
