import React, { FC } from "react";
import classNames from "classnames";
export interface IPickupItem {
  title: string;
  isActive: boolean;
  clickEvent(name: string): void;
}

const PickupItem: FC<IPickupItem> = ({ title, isActive, clickEvent }) => {
  const pickupItem = classNames("pickup-content__item", { selected: isActive });
  return (
    <div onClick={() => clickEvent(title)} className={pickupItem}>
      {title}
    </div>
  );
};

export default PickupItem;
