import React, { FC } from "react";
import styled from "styled-components";
export interface ITopTitle {
  countProduct: number;
  price: number;
}

const Title = styled.h1`
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  margin-bottom: 15px;
`;

const TopTitle: FC<ITopTitle> = ({ countProduct, price }) => {
  return <Title>{`${countProduct} товара на ${price} ₽`}</Title>;
};

export default TopTitle;
