import React, { FC } from "react";
import "./StepIndicator.scss";
import styled from "styled-components";
import StepIndicatorItem from "./stepIndicatorItem/stepIndicatorItem";

const objInfo = [
  {
    title: "Корзина",
    id: "cart",
  },
  {
    title: "Оплата и доставка",
    id: "payment",
  },
  {
    title: "Оформление",
    id: "reg",
  },
];
export interface IStepIndicatorProp {
  step: string;
}

const StepIndicator: FC<IStepIndicatorProp> = ({ step }) => {
  return (
    <div className="stepIndicator">
      {objInfo.map((item) => (
        <StepIndicatorItem
          key={item.title}
          currentStatus={step}
          title={item.title}
          id={item.id}
        ></StepIndicatorItem>
      ))}
    </div>
  );
};

export default StepIndicator;
