import React, { FC } from "react";
import "./ControlPromotion.scss";
import classNames from "classnames";
export interface ControlPromotionProps {
  clickEvent(): void;
  additionalClass: string;
}

const ControlPromotion: FC<ControlPromotionProps> = ({
  clickEvent,
  additionalClass,
}) => {
  const controlClass = classNames("control", additionalClass);
  return (
    <div className={controlClass}>
      <div onClick={clickEvent} className="control-icon">
        <img src="./control-slider.svg" alt="" />
      </div>
    </div>
  );
};

export default ControlPromotion;
