import React, { FC } from "react";
import "./UserEmpty.scss";

export interface IUserEmptyProps {
  click?(): void;
  title: string;
}

const UserEmpty: FC<IUserEmptyProps> = ({ click, title }) => {
  return (
    <>
      <button
        data-testid="toLogin"
        onClick={click}
        className="account-available"
      >
        {title}
      </button>
    </>
  );
};

export default UserEmpty;
