import { ActionsUnion, createAction } from "./../actions-helpers";
import { Dispatch } from "redux";
import { IPromItem, UserModel } from "../../api/models";
import { SliderItemApi } from "../../api/rest/sliderItem.api";
import { IOrdered } from "./../../api/models/orderedSlider.model";
import { IProduct } from "./../../api/models/product.model";
import { ShopItemApi } from "../../api/rest/shopItems.api";
import { CityApi } from "./../../api/rest/city.api";

export const OPEN_MODAL = "[app] OPEN_MODAL";
export const CLOSE_MODAL = "[app] CLOSE_MODAL";
export const CHANGE_COUNTRY = "[app] CHANGE_COUNTRY";
export const ADD_PROMOTIONS = "[app] ADD_PROMOTIONS";
export const ADD_ORDERED = "[app] ADD_ORDERED";
export const ADD_CURRENTUSER = "[app] ADD_CURRENTUSER";
export const CLEAR_CURRENTUSER = "[app] CLEAR_CURRENTUSER";
export const ADD_PIZZA = "[app] ADD_PIZZA";
export const CURRENT_PRODUCT = "[app] CURRENT_PRODUCT";
export const FETCH_CITY_PICKUP = "[app] FETCH_SITY_PICKUP";

export const Actions = {
  openModal: (typeModal: string) => createAction(OPEN_MODAL, typeModal),
  closeModal: () => createAction(CLOSE_MODAL),
  changeCountry: (country: string) => createAction(CHANGE_COUNTRY, country),
  addPromotions: (data: IPromItem[]) => createAction(ADD_PROMOTIONS, data),
  addOrdered: (data: IOrdered[]) => createAction(ADD_ORDERED, data),
  addUser: (user: UserModel) => createAction(ADD_CURRENTUSER, user),
  clearUser: () => createAction(CLEAR_CURRENTUSER),
  addPizza: (items: Array<IProduct>) => createAction(ADD_PIZZA, items),
  currentProduct: (data: IProduct) => createAction(CURRENT_PRODUCT, data),
  fetchCity: (data: Array<string>) => createAction(FETCH_CITY_PICKUP, data),
};

export const Thunks = {
  getPromotions: () => {
    return (dispatch: Dispatch) => {
      SliderItemApi.getItemPromotion().then((response) => {
        dispatch(Actions.addPromotions(response));
      });
    };
  },
  getOrdered: () => {
    return (dispatch: Dispatch) => {
      SliderItemApi.getItemOrdered().then((response) => {
        dispatch(Actions.addOrdered(response));
      });
    };
  },
  onFetchAllItems: () => {
    return (dispatch: Dispatch) => {
      ShopItemApi.getPizza().then((response) => {
        return dispatch(Actions.addPizza(response));
      });
    };
  },
  onFetchCity: () => {
    return (dispatch: Dispatch) => {
      CityApi.getCityCart().then((response) => {
        return dispatch(Actions.fetchCity(response));
      });
    };
  },
};

export type Actions = ActionsUnion<typeof Actions>;
export type Thunks = ActionsUnion<typeof Thunks>;
