import React, { FC } from "react";
import { useDispatch } from "react-redux";
import { IProduct } from "../../../api/models";
import "./ShopItem.scss";
import ShopItemButton from "./shopItemButton/ShopItemButton";
import * as appActions from "../../../store/app";
import { ButtonItemShop } from "../../styledComponent/button";
import { ThemeProvider } from "../../../layouts/app";

export interface IShopItemProp {
  item: IProduct;
}

const ShopItem: FC<IShopItemProp> = ({ item }) => {
  const dispatch = useDispatch();
  const styleDiv = {
    backgroundColor: item.feature?.color,
  };

  const openProduct = () => {
    dispatch(appActions.Actions.openModal("product"));
    dispatch(appActions.Actions.currentProduct(item));
  };

  return (
    <div className="shop-item">
      <div className="img">
        {item.feature !== undefined && (
          <div className="feature" style={styleDiv}>
            {item.feature?.title}
          </div>
        )}
        <img src={item.img} alt="" />
      </div>
      <div className="title-wrap">
        <div className="title">{item.name}</div>
        <div className="descriptions">{item.description}</div>
      </div>
      <div className="control-wrap">
        <div className="price">{`от ${item.price} ₽`}</div>
        <ThemeProvider.Consumer>
          {([theme]) => (
            <ButtonItemShop
              theme={theme}
              onClick={openProduct}
            >{`Выбрать`}</ButtonItemShop>
          )}
        </ThemeProvider.Consumer>
      </div>
    </div>
  );
};

export default ShopItem;
