import { ActionsUnion, createAction } from "./../actions-helpers";
import { Dispatch } from "redux";
import { CartApi } from "../../api/rest/cart.api";
import { ICart, IItemCart } from "./cart.reducer";
import { IPromoApi } from "./../../interface";
import { UserModel } from "../../api/models";

export const ADD_IN_CART = "[app] ADD_IN_CART";
export const FETCH_CART = "[app] FETCH_CART";
export const REMOVE_IN_CART = "[app] REMOVE_IN_CART";
export const PLUS_ITEM = "[app] CHANGE_COUNT";
export const ADD_PROMO = "[app] ADD_PROMO";
export const DELETE_PROMO = "[app] DELETE_PROMO";
export const CHANGE_STEP = "[app] CHANGE_STEP";
export const CREATE_ORDER = "[app] CREATE_ORDER";

export const Actions = {
  addInCart: (product: IItemCart) => createAction(ADD_IN_CART, product),
  fetchCart: (product: IItemCart[]) => createAction(FETCH_CART, product),
  changeCount: (id: string) => createAction(PLUS_ITEM, id),
  addPromocode: (promo: IPromoApi) => createAction(ADD_PROMO, promo),
  deletePromocode: () => createAction(DELETE_PROMO),
  changeStep: (step: string) => createAction(CHANGE_STEP, step),
  createOrder: (cart: ICart) => createAction(CREATE_ORDER, cart),
};

export const Thunks = {
  onIncrementCount: (product: IItemCart[], id: string) => {
    return (dispatch: Dispatch) => {
      CartApi.incrementItem(product, id).then((response) => {
        dispatch(Actions.fetchCart(response));
      });
    };
  },
  onDecrementCount: (product: IItemCart[], id: string) => {
    return (dispatch: Dispatch) => {
      CartApi.decrementItem(product, id).then((response) => {
        dispatch(Actions.fetchCart(response));
      });
    };
  },
  onDeleteItem: (product: IItemCart[], id: string) => {
    return (dispatch: Dispatch) => {
      CartApi.deleteItem(product, id).then((response) => {
        dispatch(Actions.fetchCart(response));
      });
    };
  },
  addPromo: (promo: string) => {
    return (dispatch: Dispatch) => {
      CartApi.checkPromo(promo).then((response) => {
        dispatch(Actions.addPromocode(response));
      });
    };
  },
  createorder: (information: ICart, user: UserModel) => {
    return (dispatch: Dispatch) => {
      CartApi.createOrder(information, user).then((response) => {
        dispatch(Actions.createOrder(response));
      });
    };
  },
};

export type Actions = ActionsUnion<typeof Actions>;
export type Thunks = ActionsUnion<typeof Thunks>;
