import React, { useState } from "react";
import ClicableDiv from "../../../commonComponent/clicableDiv/ClicableDiv";
import "./FavoritePlace.scss";
import * as appAction from "../../../../store/app";
import { useDispatch } from "react-redux";

const FavoritePlace = () => {
  const dispatch = useDispatch();
  const [favoritePlace, setfavoritePlace] = useState([
    "Москва",
    "Санкт-Петербург",
  ]);

  const changeCity = (name) => {
    dispatch(appAction.Actions.changeCountry(name));
    dispatch(appAction.Actions.closeModal());
  };

  return (
    <div className="fovarite">
      {favoritePlace.map((item, index) => {
        return (
          <ClicableDiv
            clickEvent={changeCity}
            key={index}
            name={item}
          ></ClicableDiv>
        );
      })}
    </div>
  );
};

export default FavoritePlace;
