import React from "react";
import { useDispatch, useSelector } from "react-redux";
import "./Location.scss";
import * as appActions from "../../../../store/app";
import { EModal } from "../../../../api/enums/modal";
import { RootState } from "../../../../store";

const Location = () => {
  const dispatch = useDispatch();
  const country = useSelector<RootState, string>((state) => state.app.country);
  const click = () => {
    dispatch(appActions.Actions.openModal(EModal.City));
  };
  return (
    <div className="location">
      <div className="location__title">Доставка пиццы</div>
      <div onClick={click} className="location__city">
        {country}
      </div>
    </div>
  );
};

export default Location;
