import React, { FC } from "react";
import "./ClicableItem.scss";
import classNames from "classnames";
export interface IClicableItem {
  title: string;
  clickEvent(string): void;
  id: string;
  currentState: string;
}

const TabsItem: FC<IClicableItem> = ({
  title,
  clickEvent,
  id,
  currentState,
}) => {
  const click = () => {
    clickEvent(id);
  };
  const itemClass = classNames("tabs-item", {
    active: id === currentState,
  });
  return (
    <div onClick={click} className={itemClass}>
      <div className="title">{title}</div>
    </div>
  );
};

export default TabsItem;
