import React from "react";
import { connect, ConnectedProps } from "react-redux";
import styled from "styled-components";
import {
  EDoughRus,
  EWeight,
  EWidth,
} from "../../../../../api/enums/pizza-enum";
import { IProduct } from "../../../../../api/models";
import { DispatchThunk } from "../../../../../store";
import { IItemCart } from "../../../../../store/cart";
import * as cartActions from "../../../../../store/cart/cart.actions";
import * as cartSelector from "../../../../../store/cart/cart.selectors";
import FilledCartItem from "../cartItem/FilledCartItem";
import Promocode from "../promo/Promocode";
import TopTitle from "../topTitle/TopTitle";
import ButtonNextStep from "./ButtonNextStep/buttonNextStep";

const WrapItem = styled.div`
  fisplay: flex;
  flex-direction: column;
  max-height: 475px;
  overflow: auto;
  margin-bottom: 10px;
`;

const mapStateToProps = (state) => {
  return {
    items: cartSelector.selectGetAllProduct(state),
    totalCount: cartSelector.selectGetLengthCart(state),
    totalPrice: cartSelector.selectTotalPrice(state),
  };
};
const mapDispatchToProps = (dispatch: DispatchThunk) => {
  return {
    onIncrementItem: (products: IItemCart[], id: string) => {
      dispatch(cartActions.Thunks.onIncrementCount(products, id));
    },
    onDecrementItem: (product: IItemCart[], id: string) => {
      dispatch(cartActions.Thunks.onDecrementCount(product, id));
    },
    onDeleteItem: (product: IItemCart[], id: string) => {
      dispatch(cartActions.Thunks.onDeleteItem(product, id));
    },
    onNextStep: (step: string) => {
      dispatch(cartActions.Actions.changeStep(step));
    },
  };
};
const Connector = connect(mapStateToProps, mapDispatchToProps);

type PropsFromRedux = ConnectedProps<typeof Connector>;

const CartStepOne = Connector((props: PropsFromRedux) => {
  const setTitle = (item: IProduct) => {
    if (item.options.size !== undefined) {
      return `${EWidth[item.options.size]} см, ${
        EDoughRus[item.options.dough]
      } тесто, ${EWeight[item.options.size]} г.`;
    }
    if (item.options.volume !== undefined) {
      return `${item.options.volume} л.`;
    }
    return "";
  };

  const incrementItem = (id: string) => {
    props.onIncrementItem(props.items, id);
  };
  const decrementItem = (id: string) => {
    props.onDecrementItem(props.items, id);
  };

  const deleteItem = (id: string) => {
    props.onDeleteItem(props.items, id);
  };

  const nextStep = () => {
    props.onNextStep("payment");
  };

  return (
    <div className="cart-margin">
      <TopTitle
        countProduct={props.totalCount}
        price={Math.round(props.totalPrice)}
      />
      <WrapItem>
        {props.items.map((item) => (
          <FilledCartItem
            key={item.id}
            increment={incrementItem}
            decrement={decrementItem}
            optionsG={setTitle}
            product={item}
            deleteItem={deleteItem}
          />
        ))}
      </WrapItem>
      <Promocode></Promocode>
      <ButtonNextStep
        disabled={true}
        event={nextStep}
        title="Выбор способа оплаты"
      ></ButtonNextStep>
    </div>
  );
});

export default CartStepOne;
