import React, { FC } from "react";
import PickupItem from "./pickupItem/PickupItem";
export interface IPickupProps {
  cities: Array<string>;
  currentCity: string;
  clickEvent(name: string): void;
}

const Pickup: FC<IPickupProps> = ({ cities, currentCity, clickEvent }) => {
  return (
    <>
      {cities.map((city) => {
        if (city === currentCity) {
          return (
            <PickupItem
              key={city}
              clickEvent={clickEvent}
              isActive={true}
              title={city}
            />
          );
        } else {
          return (
            <PickupItem
              key={city}
              clickEvent={clickEvent}
              isActive={false}
              title={city}
            />
          );
        }
      })}
    </>
  );
};

export default Pickup;
