import React, { FC } from "react";
import { IProduct } from "../../../../api/models";
import "./ProductRight.scss";
import { IDops, IOptions } from "../Product";
import { EDough, EDoughRus, ESize } from "../../../../api/enums/pizza-enum";
import SubInfo from "../subtitleInfo/subInfo";
import IngProductRight from "./IngProductRight/IngProductRight";
import Switcher from "./switcher/Switcher";
import { ESizeRus } from "./../../../../api/enums/pizza-enum";
import Sides from "./Sides/Sides";
import { ISideModify } from "./../Product";
import ProductButton from "./productButton/ProductButton";
import { ButtonProduct } from "../../../styledComponent/button";
import { ThemeProvider } from "../../../../layouts/app";
export interface IProductRight {
  item: IProduct;
  options: IOptions;
  dops: IDops[];
  ingDop(title: string, status: boolean): void;
  switcherItemEvent(title: string, type: string): void;
  sides: ISideModify[];
  changeSides(name: string, choose: boolean);
  subTitle: string;
  clickButton(): void;
}

const ProductRight: FC<IProductRight> = ({
  item,
  options,
  dops,
  ingDop,
  switcherItemEvent,
  sides,
  changeSides,
  subTitle,
  clickButton,
}) => {
  return (
    <div className="right">
      <div className="title-wrap">
        <div className="title">{item.name}</div>
        <div className="info"></div>
        <SubInfo title={subTitle}></SubInfo>
        <div className="ing">
          {dops.map((item) => (
            <IngProductRight
              key={item.title}
              clickEvent={ingDop}
              title={item.title}
              isSelect={item.isSelect}
            />
          ))}
        </div>
        {options.size !== undefined && (
          <Switcher
            enum={ESize}
            enumRus={ESizeRus}
            switcherItemEvent={switcherItemEvent}
            name="size"
          ></Switcher>
        )}
        {options.dough !== undefined && (
          <Switcher
            enum={EDough}
            enumRus={EDoughRus}
            switcherItemEvent={switcherItemEvent}
            name="dough"
          ></Switcher>
        )}
        {item.sides.length !== 0 && (
          <Sides changeSide={changeSides} sides={sides}></Sides>
        )}
      </div>
      <ThemeProvider.Consumer>
        {([theme]) => (
          <ButtonProduct
            theme={theme}
            onClick={clickButton}
          >{`Добавить в корзину за ${String(item.price)} ₽`}</ButtonProduct>
        )}
      </ThemeProvider.Consumer>
    </div>
  );
};

export default ProductRight;
