/* eslint-disable no-throw-literal */
import { http } from "../http";
import { UserModel, defaultUsers } from "../models";

export class UsersApi {
  static async getOrCreateUsers(): Promise<UserModel[]> {
    const response = defaultUsers;
    return response;
  }

  static async updateUsers(data: UserModel[]): Promise<UserModel[]> {
    const response = await http.put(`/users.json`, data);

    return response.data;
  }
  static async checkUser(login: string, password: string): Promise<UserModel> {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const data = defaultUsers;
        const user = data.find(
          (item) => item.login === login && item.password === password
        );
        try {
          this.checkError(user);
          if (user) {
            localStorage.setItem("user", JSON.stringify(user));
            resolve(user);
          }
        } catch (error) {
          reject(error);
        }
      }, 4000);
    });
  }
  static checkError(user: UserModel | undefined) {
    const serverCasino: boolean = Math.random() < 0.2;
    if (serverCasino) throw "Ошибка сервера, повторите попытку";
    if (user === undefined) throw "Не правильный логин или пароль";
    if (user.isBanned === true) throw "Пользователь забанен";
  }
}
