import React, { FC, useState } from "react";
import "./PromotionItem.scss";

export interface IPromotionItemProps {
  url: string;
}

const PromotionItem: FC<IPromotionItemProps> = ({ url }) => {
  const style = {
    backgroundImage: `url('${url}')`,
  };
  return (
    <div className="promotionItem">
      <div className="content" style={style}></div>
      <div className="border"></div>
    </div>
  );
};

export default PromotionItem;
