import React, { FC } from "react";
import SvgLogo from "../../../commonComponent/svg/SvgLogo";
import "./Logo.scss";

interface ILogoProps {
  viewBoxWidth: number;
  additionalClass?: string;
}

const Logo: FC<ILogoProps> = ({ viewBoxWidth, additionalClass }) => {
  return (
    <div className={`logo ${additionalClass}`}>
      <SvgLogo viewBoxWidth={viewBoxWidth}></SvgLogo>
    </div>
  );
};

export default Logo;
