import React, { FC } from "react";
import "./FilledCartItem.scss";
import styled from "styled-components";
import { ICart } from "./../../../../../store/cart/cart.reducer";
import { IAddInCart } from "../../../product/Product";
import { IProduct } from "../../../../../api/models";
import FilledCartItemButton from "./../FilledCartButtonItem/FilledCartItemButton";
import SvgClose from "../../../../commonComponent/svg/SvgClose";

const CartItemTop = styled.div`
  display: flex;
  border-bottom: 1px solid rgb(226, 226, 233);
  padding-bottom: 8px;
`;
const CartItemBottom = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 12px 0px;
`;
const ContainerInfo = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

export interface IFilledCartItemProps {
  product: IAddInCart;
  optionsG(item: IProduct): string;
  increment(id: string): void;
  decrement(id: string): void;
  deleteItem(id: string): void;
}

const FilledCartItem: FC<IFilledCartItemProps> = ({
  product,
  optionsG,
  decrement,
  increment,
  deleteItem,
}) => {
  return (
    <div className="cartItem">
      <CartItemTop>
        <button
          className="cartItem__svg"
          onClick={() => deleteItem(product.id)}
        >
          <SvgClose></SvgClose>
        </button>
        <img className="cartItem__img" src={product.item.img} alt="" />
        <ContainerInfo>
          <div className="cartItem__name">{product.item.name}</div>
          <div className="cartItem__options">{optionsG(product.item)}</div>
        </ContainerInfo>
      </CartItemTop>
      <CartItemBottom>
        <div className="cartItem__price">{`${Math.round(
          product.item.price * product.counts
        )} ₽`}</div>
        <div className="cartItem__buttonGroup">
          <FilledCartItemButton
            idItem={product.id}
            eventClick={decrement}
            title="-"
          />
          <div className="cartItem__count">{product.counts}</div>
          <FilledCartItemButton
            idItem={product.id}
            eventClick={increment}
            title="+"
          />
        </div>
      </CartItemBottom>
    </div>
  );
};

export default FilledCartItem;
