import React, { useState } from "react";
import PromoButton from "./promoButton/PromoButton";
import "./Promocode.scss";
import * as cartActions from "../../../../../store/cart";
import { connect, ConnectedProps } from "react-redux";
import { DispatchThunk } from "../../../../../store";
import SvgPromoTrue from "../../../../commonComponent/svg/SvgPromoTrue";
import classNames from "classnames";

const mapStateToProps = (state) => {
  return {
    promocode: cartActions.selectGetPromocode(state),
    promocodeError: cartActions.selectGetPromocodeError(state),
  };
};

const mapDispatchToProps = (dispatch: DispatchThunk) => {
  return {
    addPromocode: (promo: string) => {
      dispatch(cartActions.Thunks.addPromo(promo));
    },
    deletePromocode: () => {
      dispatch(cartActions.Actions.deletePromocode());
    },
  };
};
const Connector = connect(mapStateToProps, mapDispatchToProps);

type PropsFromRedux = ConnectedProps<typeof Connector>;

const Promocode = Connector((props: PropsFromRedux) => {
  const [value, setValue] = useState(props.promocode);
  const actPromocode = () => {
    props.addPromocode(value);
  };

  const clearPromocode = () => {
    props.deletePromocode();
  };
  const changeValue = (e: React.FormEvent<HTMLInputElement>) => {
    setValue(e.currentTarget.value);
  };
  const inputClass = classNames("promocode-input", {
    promoTrue: props.promocode !== "",
  });
  return (
    <div className="promocode-div">
      {props.promocode !== "" && <SvgPromoTrue></SvgPromoTrue>}
      <input
        className={inputClass}
        onChange={changeValue}
        value={value}
        disabled={props.promocode !== "" ? true : false}
        placeholder="Промокод"
        type="text"
      />
      {props.promocode === "" ? (
        <PromoButton title={`Применить`} click={actPromocode}></PromoButton>
      ) : (
        <PromoButton title={`Сбросить`} click={clearPromocode}></PromoButton>
      )}
      <div className="errorBlock">{props.promocodeError}</div>
    </div>
  );
});

export default Promocode;
