import React from "react";
import { connect, ConnectedProps } from "react-redux";
import EmptyCart from "./emptyCart/EmptyCart";
import * as cartSelector from "../../../store/cart";
import FilledCart from "./FilledCart/FilledCart";
import "./Cart.scss";

const mapStateToProps = (state) => {
  return {
    length: cartSelector.selectGetLengthCart(state),
  };
};

const Connector = connect(mapStateToProps);
type PropsFromRedux = ConnectedProps<typeof Connector>;

const Cart = Connector((props: PropsFromRedux) => {
  return <div>{props.length === 0 ? <EmptyCart /> : <FilledCart />}</div>;
});

export default Cart;
