import React from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { UserModel } from "../../../../api/models";
import { RootState } from "../../../../store";
import "./Account.scss";
import UserAvailable from "./userEmpty/UserEmpty";
import UserEmpty from "./userAvailable/UserAvailable";

const Account = () => {
  const navigate = useNavigate();
  const currentUser = useSelector<RootState, UserModel | null>(
    (state) => state.app.user
  );

  const clickEvent = () => {
    navigate("/login");
  };

  const navigateToCart = () => {
    navigate("/profile");
  };

  return (
    <>
      {currentUser !== null ? (
        <UserEmpty
          redirectToProfile={navigateToCart}
          word={currentUser.name[0].toUpperCase()}
        ></UserEmpty>
      ) : (
        <UserAvailable title="Войти" click={clickEvent}></UserAvailable>
      )}
    </>
  );
};

export default Account;
