import * as errorActions from "./errorMessage.actions";

export interface IErrorReducer {
  errorMessage: string;
}

const initialState: IErrorReducer = {
  errorMessage: "",
};

export const errorReducer = (
  state = initialState,
  action: errorActions.Actions
): IErrorReducer => {
  switch (action.type) {
    case errorActions.ERROR_OPEN:
      return {
        ...state,
        errorMessage: action.payload,
      };
    case errorActions.ERROR_CLOSE:
      return {
        ...state,
        errorMessage: "",
      };
    default:
      return state;
  }
};
