import * as loadingActions from "./loadingLoginForm.actions";

export interface ILoading {
  isLoading: boolean;
}

const initialState: ILoading = {
  isLoading: false,
};

export const loadingReducer = (
  state = initialState,
  action: loadingActions.Actions
): ILoading => {
  switch (action.type) {
    case loadingActions.START_LOADING:
      return {
        ...state,
        isLoading: true,
      };
    case loadingActions.FINISH_LOADING:
      return {
        ...state,
        isLoading: false,
      };
    default:
      return state;
  }
};
