import styled from "styled-components";
import { IThemeItem } from "../themeContext/ThemeContext";
export interface IPropsTheme {
  theme: IThemeItem;
}
export const ButtonOrange = styled.button<IPropsTheme>`
  height: 40px;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 24px;
  background-color: ${(props: IPropsTheme) => props.theme.background};
  color: rgb(255, 255, 255);
  outline: none;
  border: none;
  border-radius: 9999px;
  text-align: center;
  &:hover {
    background-color: rgb(209, 100, 22);
    cursor: pointer;
  }
`;
export const ButtonItemShop = styled.button<IPropsTheme>`
  height: 40px;
  min-width: 120px;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 24px;
  background-color: ${(props: IPropsTheme) =>
    props.theme.backgroundButtonItemShop};
  color: rgb(209, 87, 0);
  outline: none;
  border: 0;
  border-radius: 20px;
  height: 40px;
  &:hover {
    background-color: rgb(255, 210, 179);
    color: rgb(209, 87, 0);
    cursor: pointer;
  }
`;

export const ButtonProduct = styled.button<IPropsTheme>`
  height: 48px;
  padding: 12px 24px;
  font-size: 16px;
  line-height: 24px;
  width: 100%;
  margin-top: 40px;
  background-color: ${(props: IPropsTheme) =>
    props.theme.backgroundButtonProduct};
  color: rgb(255, 255, 255);
  outline: none;
  border: none;
  border-radius: 9999px;
  text-align: center;
  font-weight: 500;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  transition-property: background-color, color;
  transition-duration: 200ms;
  transition-timing-function: ease-out;
`;
