import { ICart, IItemCart } from "./../../store/cart/cart.reducer";
import { IPromoApi } from "./../../interface";
import { promo } from "../models/cart.model";
import { UserModel } from "../models";
export class CartApi {
  static async incrementItem(
    items: IItemCart[],
    id: string
  ): Promise<IItemCart[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const product = items.map((item) => {
          if (item.id === id) return { ...item, counts: item.counts + 1 };
          return item;
        });
        resolve(product);
      }, 100);
    });
  }

  static async decrementItem(
    items: IItemCart[],
    id: string
  ): Promise<IItemCart[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const product = items.map((item) => {
          if (item.id === id && item.counts !== 1)
            return { ...item, counts: item.counts - 1 };
          return item;
        });
        resolve(product);
      }, 100);
    });
  }
  static async deleteItem(
    items: IItemCart[],
    id: string
  ): Promise<IItemCart[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const product = items.filter((item) => item.id !== id);
        resolve(product);
      }, 100);
    });
  }
  static async checkPromo(promocode: string): Promise<IPromoApi> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const returnData: IPromoApi = {
          promocode: "",
          promoError: "",
        };
        const data = promo;
        data.forEach((item) => {
          if (item === promocode) {
            returnData.promocode = promocode;
            returnData.promoError = "";
          }
        });
        if (returnData.promocode === "") {
          returnData.promoError = "Не правильный промокод";
        }
        resolve(returnData);
      }, 100);
    });
  }
  static async createOrder(
    information: ICart,
    currentUser: UserModel
  ): Promise<ICart> {
    return new Promise((resolve) => {
      setTimeout(() => {
        information.idOrder = String(Date.now());
        information.step = "reg";
        if (localStorage.getItem(String(currentUser.id)) === null) {
          const arrayOrder: ICart[] = [];
          arrayOrder.push(information);
          localStorage.setItem(
            String(currentUser.id),
            JSON.stringify(arrayOrder)
          );
        } else {
          const arrayOrder: ICart[] = JSON.parse(
            localStorage.getItem(String(currentUser.id)) || "{}"
          );
          arrayOrder.push(information);
          localStorage.setItem(
            String(currentUser.id),
            JSON.stringify(arrayOrder)
          );
        }
        resolve(information);
      }, 100);
    });
  }
}
