import React, { PropsWithChildren, useState } from "react";
import { BrowserRouter } from "react-router-dom";
import {
  ITheme,
  IThemeItem,
  themes,
} from "../components/themeContext/ThemeContext";
import Routers from "../router";

type IContextType = [IThemeItem, () => void, string];

export const ThemeProvider = React.createContext<IContextType>([
  themes.light,
  () => null,
  "",
]);

const Theme = (props: PropsWithChildren<{}>) => {
  const [theme, setTheme] = useState(themes.light);
  const [themeName, setThemeName] = useState<string>("Светлая");
  const toggleTheme = () => {
    if (themes["light"] === theme) {
      setTheme(themes.dark);
      setThemeName("Тёмная");
    }
    if (themes["dark"] === theme) {
      setTheme(themes.light);
      setThemeName("Светлая");
    }
  };
  return (
    <>
      <ThemeProvider.Provider value={[theme, toggleTheme, themeName]}>
        <BrowserRouter>
          <Routers />
          {props.children}
        </BrowserRouter>
      </ThemeProvider.Provider>
    </>
  );
};

export default Theme;
