import React from "react";
import SvgSearchInput from "../../../commonComponent/svg/SvgSearchInput";
import "./SearchInput.scss";

const SearchInput = () => {
  return (
    <div className="searchInput-wrap">
      <input
        data-testid="city"
        className="searchInput"
        type="text"
        placeholder="Поиск..."
      />
      <SvgSearchInput />
    </div>
  );
};

export default SearchInput;
