import React, { FC } from "react";
import { IOrdered } from "../../../../api/models/orderedSlider.model";
import "./OrderedItem.scss";

const OrderedItem: FC<Partial<IOrdered>> = ({ img, name, price }) => {
  return (
    <div className="orderedItem">
      <img src={img} alt="" />
      <div className="information">
        <div className="information__name">{name}</div>
        <div className="information__price">{`от ${price} ₽`}</div>
      </div>
    </div>
  );
};

export default OrderedItem;
