import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import CartButton from "./cartButton/CartButton";
import Menu from "./menu/Menu";
import "./Nav.scss";
import * as appActions from "../../../store/app";
import * as modalEnum from "../../../api/enums/modal";
import { RootState } from "../../../store";
import classNames from "classnames";
import { ButtonOrange } from "../../styledComponent/button";
import { ThemeProvider } from "../../../layouts/app";

const Nav = () => {
  const dispatch = useDispatch();
  const cartLength = useSelector<RootState, number>(
    (state) => state.cart.items.length
  );
  const [isScrolled, setIsScrolled] = useState(false);

  const nav = useRef<HTMLElement>(null);

  const scroll = () => {
    if (nav !== null && nav.current !== null) {
      const cord = nav.current.getBoundingClientRect().y;
      if (cord === 0) {
        return true;
      } else {
        return false;
      }
    }
  };

  const cartClick = () => {
    dispatch(appActions.Actions.openModal(modalEnum.EModal.Cart));
  };

  const changeClass = () => {
    const result = scroll();
    if (result && isScrolled !== true) {
      return setIsScrolled(true);
    } else {
      if (!result) {
        return setIsScrolled(false);
      }
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", changeClass);
    return () => {
      window.removeEventListener("scroll", changeClass);
    };
  }, []);

  const navClass = classNames("nav", {
    scrolled: isScrolled,
  });

  return (
    <nav ref={nav} onScroll={scroll} className={navClass}>
      <div className="wrapp">
        <Menu></Menu>
        <ThemeProvider.Consumer>
          {([theme, toggleTheme, themeName]) => (
            <button data-testid="home" onClick={toggleTheme}>
              {themeName}
            </button>
          )}
        </ThemeProvider.Consumer>
        <ThemeProvider.Consumer>
          {([theme]) => (
            <ButtonOrange
              theme={theme}
              onClick={cartClick}
            >{`Корзина  (${cartLength})`}</ButtonOrange>
          )}
        </ThemeProvider.Consumer>
      </div>
    </nav>
  );
};

export default Nav;
