import React, { useCallback, useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { promotionItem } from "../../../../api/models/promotionItems.model";
import { RootState } from "../../../../store";
import OrderedItem from "./../orderedItem/OrderedItem";
import "./OrderedSlider.scss";
import * as appActions from "../../../../store/app";
import { IOrdered } from "../../../../api/models/orderedSlider.model";

const OrderedSlider = () => {
  const dispatch = useDispatch();
  const items = useSelector<RootState, IOrdered[]>(
    (state) => state.app.ordered
  );
  const [translate, setTranslate] = useState(0);
  const [clickX, setClickX] = useState<number>(0);
  const [isMouseDown, setIsMouseDown] = useState<boolean>(false);
  const [minPx, setMinPx] = useState<number>(0);
  const [maxPx, setMaxPx] = useState<number>(0);
  const [widthBlock, setWidthBlock] = useState<number>(0);
  const ref = useRef<HTMLDivElement>(null);
  const styleSlider = {
    transform: `translateX(${translate}px)`,
  };
  const mouseMove = useCallback(
    (e: React.FormEvent<HTMLDivElement>) => {
      const a = e as React.MouseEvent<HTMLDivElement>;
      setTranslate((prev) => prev + -(clickX - a.nativeEvent.offsetX));
    },
    [clickX]
  );
  const mouseDown = useCallback((e: React.MouseEvent<HTMLDivElement>) => {
    setIsMouseDown(true);
    setClickX(e.nativeEvent.offsetX);
  }, []);

  const mouseUp = () => {
    setIsMouseDown(false);
    if (translate > minPx) setTranslate(minPx);
    if (translate < -maxPx + widthBlock) setTranslate(-maxPx + widthBlock);
  };

  const dragStart = () => {
    return false;
  };
  useEffect(() => {
    dispatch(appActions.Thunks.getOrdered());
  }, []);
  useEffect(() => {
    if (ref.current) {
      setWidthBlock(ref.current.offsetWidth);
      setMaxPx(ref.current.scrollWidth);
    }
  }, [items]);
  return (
    <div className="orderSlider">
      <div
        className="orderSlider__container"
        ref={ref}
        onMouseDown={mouseDown}
        onMouseMove={isMouseDown ? mouseMove : undefined}
        onMouseUp={mouseUp}
        onDragStart={dragStart}
        style={styleSlider}
      >
        {items.map((item) => (
          <OrderedItem
            key={item.id}
            name={item.name}
            img={item.img}
            price={item.price}
          />
        ))}
      </div>
    </div>
  );
};

export default OrderedSlider;
