import React from "react";
import { useDispatch } from "react-redux";
import CartButton from "../../../mainpage/nav/cartButton/CartButton";
import * as appActions from "../../../../store/app";
import "./EmptyCart.scss";
import { ButtonOrange } from "../../../styledComponent/button";
import { ThemeProvider } from "../../../../layouts/app";

const EmptyCart = () => {
  const dispatch = useDispatch();

  const clickEventButton = () => {
    dispatch(appActions.Actions.closeModal());
  };

  return (
    <div className="emptyCart">
      <img src="dog_cart.svg" alt="" />
      <div className="title">Ой, пусто!</div>
      <div className="subtitle">
        Ваша корзина пуста, откройте «Меню» и выберите понравившийся товар. Мы
        доставим ваш заказ от 465 ₽
      </div>
      <ThemeProvider.Consumer>
        {([theme]) => (
          <ButtonOrange
            theme={theme}
            onClick={clickEventButton}
          >{`Вернуться в меню`}</ButtonOrange>
        )}
      </ThemeProvider.Consumer>
    </div>
  );
};

export default EmptyCart;
