import React, { FC } from "react";
import styled from "styled-components";
export interface IButtonNextStepProp {
  title: string;
  event(): void;
  disabled: boolean;
}

const Button = styled.button`
  width: 100%;
  height: 40px;
  background-color: orange;
  border-radius: 50px;
  color: black;
  font-size: 18px;
  margin-top: 20px;
  &:hover {
    background-color: #ff6900;
    cursor: pointer;
  }
`;

const ButtonNextStep: FC<IButtonNextStepProp> = ({
  title,
  event,
  disabled,
}) => {
  return (
    <Button disabled={!disabled} onClick={event}>
      {title}
    </Button>
  );
};

export default ButtonNextStep;
