import React, { useEffect } from "react";
import Header from "../header/Header";
import Nav from "../nav/Nav";
import OftenOrdered from "../oftenOrdered/OftenOrdered";
import Promotion from "../promotion/Promotion";
import Shop from "../shop/Shop";
import "./PageMain.scss";
import * as appSelector from "../../../store/app/app.selectors";
import * as appAction from "../../../store/app/app.actions";
import { DispatchThunk } from "../../../store";
import { UserModel } from "../../../api/models";
import { connect, ConnectedProps } from "react-redux";

const mapToStateProps = (state) => {
  return {
    user: appSelector.selectGetCurrentUser(state),
  };
};

const mapToDispatch = (dispatch: DispatchThunk) => {
  return {
    fetchUserFromLocalStorage: (user: UserModel) => {
      dispatch(appAction.Actions.addUser(user));
    },
  };
};

const Connector = connect(mapToStateProps, mapToDispatch);

type PropsFromRedux = ConnectedProps<typeof Connector>;

const PageMain = Connector((props: PropsFromRedux) => {
  const checkUserFromLocalStorage = () => {
    if (localStorage.getItem("user") !== null) {
      return JSON.parse(localStorage.getItem("user") || "{}");
    }
  };

  useEffect(() => {
    if (checkUserFromLocalStorage() !== undefined) {
      props.fetchUserFromLocalStorage(checkUserFromLocalStorage());
    }
  }, []);
  return (
    <div className="main">
      <Header></Header>
      <Nav></Nav>
      <Promotion></Promotion>
      <OftenOrdered></OftenOrdered>
      <Shop></Shop>
    </div>
  );
});

export default PageMain;
