import React, { createElement, FormEvent, useEffect } from "react";
import ReactDOM from "react-dom";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../store";
import City from "./city/City";
import "./Modal.scss";
import * as appActions from "../../store/app";
import Cart from "./cart/Cart";
import Product from "./product/Product";
import { IPizza } from "../../interface";

const Modal = () => {
  const dispatch = useDispatch();
  let modalDiv = document.createElement("div");
  const modalStatus = useSelector<RootState, string>(
    (state) => state.app.modal
  );

  const switchContent = () => {
    if (modalStatus === "") return <></>;

    switch (modalStatus) {
      case "city":
        return <City />;
      case "cart":
        return <Cart />;
      case "product":
        return <Product />;
      default:
        return <></>;
    }
  };

  const click = (e: FormEvent<HTMLDivElement>) => {
    if (e.currentTarget === e.target) dispatch(appActions.Actions.closeModal());
  };

  useEffect(() => {
    document.body.appendChild(modalDiv);
    return () => {
      document.body.removeChild(modalDiv);
    };
  }, []);

  return ReactDOM.createPortal(
    <div onClick={click} data-testid="modalClick" className="modal">
      <div className="modal__body">{switchContent()}</div>
    </div>,
    modalDiv
  );
};
export default Modal;
