import React, { useCallback, useEffect, useRef, useState } from "react";
import classNames from "classnames";
import ControlPromotion from "./controlPromotion/ControlPromotion";
import "./Promotion.scss";
import { IPromItem } from "../../../api/models/promotionItems.model";
import { RootState } from "../../../store";
import { useDispatch, useSelector } from "react-redux";
import PromotionItem from "./promotionItem/PromotionItem";
import * as appActions from "../../../store/app";

const Promotion = () => {
  const dispatch = useDispatch();
  const [translate, setTranslate] = useState<number>(0);
  const [minPx, setMinPx] = useState<number>(0);
  const [maxPx, setMaxPx] = useState<number>(0);
  const promItems = useSelector<RootState, IPromItem[]>(
    (state) => state.app.promotions
  );
  const [isMouseDown, setIsMouseDown] = useState<boolean>(false);
  const [clickX, setClickX] = useState<number>(0);
  const ref = useRef<HTMLDivElement>(null);
  const clickLeft = () => {
    setTranslate((prev) => prev + maxPx / promItems.length);
  };

  const clickRight = () => {
    setTranslate((prev) => prev - maxPx / promItems.length);
  };

  const mouseMove = useCallback(
    (e: React.FormEvent<HTMLDivElement>) => {
      const a = e as React.MouseEvent<HTMLDivElement>;
      setTranslate((prev) => prev + -(clickX - a.nativeEvent.offsetX));
    },
    [clickX]
  );
  const mouseDown = useCallback((e: React.MouseEvent<HTMLDivElement>) => {
    setIsMouseDown(true);
    setClickX(e.nativeEvent.offsetX);
  }, []);

  const mouseUp = useCallback(() => {
    setIsMouseDown(false);
    if (translate > 0) setTranslate(0);
    if (translate < -maxPx / 2) setTranslate(-maxPx / 2);
  }, [translate, maxPx]);

  const dragStart = () => {
    return false;
  };

  const promotionStyle = {
    transform: `translateX(${translate}px)`,
  };

  useEffect(() => {
    dispatch(appActions.Thunks.getPromotions());
  }, []);

  useEffect(() => {
    if (ref.current) setMaxPx(ref.current.offsetWidth);
  }, [promItems]);

  const promotionClass = classNames("promotion__body", {
    transition: !isMouseDown,
  });

  return (
    <div className="promotion--wrap">
      {translate !== minPx && translate < 0 ? (
        <ControlPromotion
          clickEvent={clickLeft}
          additionalClass="control-left"
        ></ControlPromotion>
      ) : (
        ""
      )}
      {translate !== -(maxPx / 2) && translate > -(maxPx / 2) ? (
        <ControlPromotion
          clickEvent={clickRight}
          additionalClass="control-right"
        ></ControlPromotion>
      ) : (
        ""
      )}

      <div className="promotion">
        <div
          className={promotionClass}
          onMouseDown={mouseDown}
          onMouseMove={isMouseDown ? mouseMove : undefined}
          onMouseUp={mouseUp}
          onDragStart={dragStart}
          ref={ref}
          style={promotionStyle}
        >
          {promItems.map((el, index) => (
            <PromotionItem url={el.url} key={index} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Promotion;
