import React from "react";
import * as cart from "../../../../../store/cart";
import { connect, ConnectedProps } from "react-redux";
import CommonTitle from "../../../../commonComponent/commonTitle/CommonTitle";
const mapStateToProps = (state) => {
  return {
    id: cart.selectGetID(state),
  };
};
const Connector = connect(mapStateToProps);

type PropsFromRedux = ConnectedProps<typeof Connector>;

const CartStepLast = Connector((props: PropsFromRedux) => {
  return (
    <div className="lastStep">
      <CommonTitle
        title={`Заказ оформлен. Номер вашего заказа:${props.id}`}
      ></CommonTitle>
    </div>
  );
});

export default CartStepLast;
