import React, { useEffect, useState } from "react";
import { connect, ConnectedProps } from "react-redux";
import * as appSelectors from "../../../../../store/app/app.selectors";
import * as appActions from "../../../../../store/app";
import * as cart from "../../../../../store/cart";
import { useNavigate } from "react-router-dom";
import { DispatchThunk } from "../../../../../store";
import "./CartStepPayment.scss";
import TabsItem from "./tabsItem/TabsItem";
import Delivery from "./delivery/Delivery";
import Pickup from "./pickup/Pickup";
import CommonTitle from "../../../../commonComponent/commonTitle/CommonTitle";
import ButtonNextStep from "../cartStepOne/ButtonNextStep/buttonNextStep";
import ConsoleElement from "../../../../commonComponent/consoleElement/ConsoleElement";
import { ICart } from "../../../../../store/cart";
import { UserModel } from "../../../../../api/models";
import consoleHOK from "../../../../HOC/consoleHOC";
export interface IDelivery {
  name: string;
  city: string;
  street: string;
  numberHouse: string;
}
const ConsoleElementWithConsole = consoleHOK(ConsoleElement);
const mapStateToProps = (state) => {
  return {
    currentUser: appSelectors.selectGetCurrentUser(state),
    cartCities: appSelectors.selectGetCityCart(state),
    allCart: cart.selectGetAllCart(state),
  };
};

const mapDispatchToProps = (dispatch: DispatchThunk) => {
  return {
    closeModal: () => {
      dispatch(appActions.Actions.closeModal());
    },
    fetchCities: () => {
      dispatch(appActions.Thunks.onFetchCity());
    },
    createOrder: (state: ICart, user: UserModel) => {
      dispatch(cart.Thunks.createorder(state, user));
    },
    onNextStep: (step: string) => {
      dispatch(cart.Actions.changeStep(step));
    },
  };
};

const Connector = connect(mapStateToProps, mapDispatchToProps);

type PropsFromRedux = ConnectedProps<typeof Connector>;

const CartStepPayment = Connector((props: PropsFromRedux) => {
  const [activeTab, setActiveTab] = useState("Delivery");
  const [valid, setValid] = useState(true);
  const [activePaymentTab, setActivePaymentTab] = useState("Cash");
  const [delivery, setDelivery] = useState<IDelivery>({
    name: "",
    city: "",
    street: "",
    numberHouse: "",
  });
  const [pickupCity, setPickupCity] = useState<string>("");
  const changeDelivery = (id: string, value: string) => {
    setDelivery({ ...delivery, [id]: value });
  };
  const navigate = useNavigate();
  useEffect(() => {
    if (props.currentUser === null) {
      navigate("/login");
      props.closeModal();
    }
    props.fetchCities();
  }, []);
  const validationForm = () => {
    if (
      activeTab === "Delivery" &&
      delivery.name !== "" &&
      delivery.city !== "" &&
      delivery.street !== "" &&
      delivery.numberHouse !== ""
    ) {
    }
  };
  const order = () => {
    const cart = {
      ...props.allCart,
      paymentChoose: activePaymentTab,
      deliveryChoose: activeTab,
    };
    activeTab === "Delivery"
      ? (cart.delivery = { ...delivery })
      : (cart.pickupCity = pickupCity);
    if (props.currentUser !== null) props.createOrder(cart, props.currentUser);
    props.onNextStep("reg");
  };
  return (
    <div className="bodyPayment">
      <CommonTitle title={`Пользователь:`}></CommonTitle>
      {props.currentUser && (
        <ConsoleElementWithConsole
          title={props.currentUser.name}
        ></ConsoleElementWithConsole>
      )}
      <CommonTitle title={`Выберите способ доставки`}></CommonTitle>
      <div className="delivery">
        <TabsItem
          id="Delivery"
          currentState={activeTab}
          title="Доставка"
          clickEvent={setActiveTab}
        ></TabsItem>
        <TabsItem
          id="Pickup"
          currentState={activeTab}
          title="Самовывоз"
          clickEvent={setActiveTab}
        ></TabsItem>
      </div>
      {activeTab === "Delivery" && (
        <div className="delivery-content">
          <Delivery
            changeDelivery={changeDelivery}
            information={delivery}
          ></Delivery>
        </div>
      )}
      {activeTab === "Pickup" && (
        <div className="pickup-content">
          <Pickup
            clickEvent={setPickupCity}
            currentCity={pickupCity}
            cities={props.cartCities}
          />
        </div>
      )}
      <CommonTitle title={`Выберите способ оплаты`}></CommonTitle>
      <div className="payment">
        <TabsItem
          id="Cash"
          currentState={activePaymentTab}
          title="Наличные"
          clickEvent={setActivePaymentTab}
        ></TabsItem>
        <TabsItem
          id="Card"
          currentState={activePaymentTab}
          title="Карта"
          clickEvent={setActivePaymentTab}
        ></TabsItem>
      </div>
      <ButtonNextStep
        disabled={valid}
        title={`Оформление`}
        event={order}
      ></ButtonNextStep>
    </div>
  );
});

export default CartStepPayment;
