import React, { FC } from "react";
import { IDelivery } from "../CartStepPayment";
export interface IDileviryProps {
  information: IDelivery;
  changeDelivery(id: string, value: string): void;
}

const Delivery: FC<IDileviryProps> = ({ information, changeDelivery }) => {
  const change = (e: React.FormEvent<HTMLInputElement>) => {
    changeDelivery(e.currentTarget.id, e.currentTarget.value);
  };
  return (
    <>
      <label className="deliveryInput">
        Имя
        <input
          onChange={change}
          type="text"
          id="name"
          value={information.name}
        />
      </label>
      <label className="deliveryInput">
        Город
        <input
          onChange={change}
          type="text"
          id="city"
          value={information.city}
        />
      </label>
      <label className="deliveryInput">
        Улица
        <input
          onChange={change}
          type="text"
          id="street"
          value={information.street}
        />
      </label>
      <label className="deliveryInput">
        Дом
        <input
          onChange={change}
          type="text"
          id="numberHouse"
          value={information.numberHouse}
        />
      </label>
    </>
  );
};

export default Delivery;
