import { createSelector } from "reselect";
import { ICart } from "./cart.reducer";
import { RootState } from "./../state.types";
export const getCart = (state: RootState) => state.cart;

export const selectGetAllProduct = createSelector(getCart, (cart: ICart) => {
  return cart.items;
});

export const selectGetAllCart = createSelector(getCart, (cart: ICart) => {
  return cart;
});

export const selectGetID = createSelector(getCart, (cart: ICart) => {
  return cart.idOrder;
});

export const selectGetLengthCart = createSelector(getCart, (cart: ICart) => {
  let totalCount = 0;
  cart.items.forEach((element) => {
    totalCount += element.counts;
  });
  return totalCount;
});

export const selectTotalPrice = createSelector(getCart, (cart: ICart) => {
  let totalPrice = 0;
  cart.items.forEach((element) => {
    totalPrice += element.counts * element.item.price;
  });
  return totalPrice;
});

export const selectGetPromocode = createSelector(getCart, (cart: ICart) => {
  return cart.promocode;
});

export const selectGetPromocodeError = createSelector(
  getCart,
  (cart: ICart) => {
    return cart.promoError;
  }
);

export const selectGetStepCart = createSelector(getCart, (cart: ICart) => {
  return cart.step;
});
