import React, { useState } from "react";
import { IMenuItemProps } from "../../../../api/models/menuItem.model";
import { defaultItem } from "../../../../api/models/menuItem.model";
import "./Menu.scss";
import MenuItem from "./menuItem/MenuItem";

const Menu = () => {
  const [itemList, setItemList] = useState<IMenuItemProps[]>(defaultItem);

  return (
    <ul className="menu">
      {itemList.map((item) => {
        return <MenuItem title={item.title} link={item.link} key={item.id} />;
      })}
    </ul>
  );
};

export default Menu;
