import { ActionsUnion, createAction } from "./../actions-helpers";

export const ERROR_OPEN = "[app] ERROR_OPEN";
export const ERROR_CLOSE = "[app] ERROR_CLOSE";
export const Actions = {
  errorOpen: (title: string) => createAction(ERROR_OPEN, title),
  errorClose: () => createAction(ERROR_CLOSE),
};

export type Actions = ActionsUnion<typeof Actions>;
