import * as models from "../models/index";
import { ICity } from "../models/index";
import { pickupCity } from "./../models/pickupCity.model";

export class CityApi {
  static async getCity(): Promise<ICity[]> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const data = models.city;
        data.sort();
        resolve(data);
      }, 1000);
    });
  }
  static async getCityCart(): Promise<Array<string>> {
    return new Promise((resolve) => {
      setTimeout(() => {
        const data = pickupCity;
        resolve(data);
      }, 500);
    });
  }
}
