import { IProduct } from "../../api/models";
import * as cartActions from "./cart.actions";
import { IDelivery } from "./../../components/modal/cart/FilledCart/cartStepPayment/CartStepPayment";

export interface IItemCart {
  id: string;
  item: IProduct;
  counts: number;
}
export interface ICart {
  items: Array<IItemCart>;
  promocode: string;
  promoError: string;
  step: string;
  paymentChoose: string;
  deliveryChoose: string;
  pickupCity: string;
  delivery: IDelivery | null;
  idOrder: string;
}

const initialState: ICart = {
  items: [],
  promocode: "",
  promoError: "",
  step: "cart",
  paymentChoose: "",
  deliveryChoose: "",
  pickupCity: "",
  delivery: null,
  idOrder: "",
};

export const cartReducer = (
  state = initialState,
  action: cartActions.Actions
): ICart => {
  switch (action.type) {
    case cartActions.ADD_IN_CART:
      return {
        ...state,
        items: [...state.items, action.payload],
      };
    case cartActions.FETCH_CART:
      return {
        ...state,
        items: action.payload,
      };
    case cartActions.ADD_PROMO:
      return {
        ...state,
        promocode: action.payload.promocode,
        promoError: action.payload.promoError,
      };
    case cartActions.DELETE_PROMO:
      return {
        ...state,
        promocode: "",
        promoError: "",
      };
    case cartActions.CHANGE_STEP:
      return {
        ...state,
        step: action.payload,
      };
    case cartActions.CREATE_ORDER:
      return {
        ...state,
        paymentChoose: action.payload.paymentChoose,
        deliveryChoose: action.payload.deliveryChoose,
        pickupCity: action.payload.pickupCity,
        delivery: action.payload.delivery,
        items: action.payload.items,
        promocode: action.payload.promocode,
        promoError: action.payload.promoError,
        step: action.payload.step,
        idOrder: action.payload.idOrder,
      };
    default:
      return state;
  }
};
