import React from "react";
import "./FilledCart.scss";
import * as cart from "../../../../store/cart";

import CartStepOne from "./cartStepOne/CartStepOne";
import StepIndicator from "./stepIndicator/StepIndicator";
import { connect, ConnectedProps } from "react-redux";
import CartStepPayment from "./cartStepPayment/CartStepPayment";
import CartStepLast from "./cartStepLast/CartStepLast";

const mapStateToProps = (state) => {
  return {
    step: cart.selectGetStepCart(state),
  };
};

const Connector = connect(mapStateToProps);

type PropsFromRedux = ConnectedProps<typeof Connector>;

const FilledCart = Connector((props: PropsFromRedux) => {
  const stepChanger = (): JSX.Element => {
    switch (props.step) {
      case "cart":
        return <CartStepOne></CartStepOne>;
      case "payment":
        return <CartStepPayment></CartStepPayment>;
      case "reg":
        return <CartStepLast></CartStepLast>;
      default:
        return <></>;
    }
  };
  return (
    <div className="filledCart">
      <StepIndicator step={props.step}></StepIndicator>
      {stepChanger()}
    </div>
  );
});

export default FilledCart;
