import React, { Suspense } from "react";
import Logo from "../../mainpage/header/logo/Logo";
import "./City.scss";
import FavoritePlace from "./favoritePlace/FavoritePlace";
import SearchInput from "./searchInput/SearchInput";

const SelectionCity = React.lazy(() => import("./selectionCity/SelectionCity"));

const City = () => {
  return (
    <>
      <Logo viewBoxWidth={95} additionalClass="city_logo"></Logo>
      <SearchInput />
      <FavoritePlace></FavoritePlace>
      <Suspense fallback={<div>Загрузка...</div>}>
        <SelectionCity />
      </Suspense>
    </>
  );
};

export default City;
