import React, { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../../store";
import "./Product.scss";
import { IProduct } from "./../../../api/models/product.model";
import ProductLeft from "./productLeft/ProductLeft";
import ProductRight from "./productRight/ProductRight";
import { additionalOption } from "../../../api/models/orderedSlider.model";
import {
  EDoughRus,
  EPrice,
  EWeight,
  EWidth,
} from "../../../api/enums/pizza-enum";
import * as cartActions from "../../../store/cart/cart.actions";
import { IItemCart } from "../../../store/cart";

export interface IOptions {
  size: string;
  dough: string;
  volume: string;
}

export interface IAddInCart {
  id: string;
  item: IProduct;
  counts: number;
}

export interface IDops {
  title: string;
  isSelect: boolean;
}

export interface ISideModify extends additionalOption {
  isChoose: boolean;
}

const Product = () => {
  const dispatch = useDispatch();
  const currentProduct = useSelector<RootState, IProduct | null>(
    (state) => state.app.currentProduct
  );
  const [options, setOptions] = useState<IOptions>({
    size: "",
    dough: "",
    volume: "",
  });
  const [item, setItem] = useState<IProduct | null>();
  const [dops, setDops] = useState<IDops[]>([]);
  const [sides, setSides] = useState<ISideModify[]>([]);
  const [initialPrice, setInitialPrice] = useState<number>(0);

  const setDopsFunc = useCallback(() => {
    const mas: IDops[] = [];
    if (currentProduct)
      currentProduct.productOption.forEach((elem) =>
        mas.push({ title: elem, isSelect: true })
      );
    return mas;
  }, [currentProduct]);

  const setSidesFunc = useCallback(() => {
    let mas: ISideModify[] = [];
    if (currentProduct)
      currentProduct.sides.forEach((elem) =>
        mas.push({ ...elem, isChoose: false })
      );
    return mas;
  }, [currentProduct]);

  const setTitle = () => {
    if (options.size !== undefined) {
      return `${EWidth[options.size]} см, ${EDoughRus[options.dough]} тесто, ${
        EWeight[options.size]
      } г.`;
    }
    if (options.volume !== undefined) {
      return `${options.volume} л.`;
    }
    return "";
  };

  const changeOption = (title: string, type: string) => {
    setOptions({ ...options, [type]: title });
  };

  const changePrice = () => {
    let additionalPrice = 0;
    sides.forEach((side) => {
      if (side.isChoose) additionalPrice += side.price;
    });
    let optionPrice =
      initialPrice + initialPrice * (EPrice[options.size] / 100);
    if (item && optionPrice)
      setItem({ ...item, price: optionPrice + additionalPrice });
  };

  const changeSide = (title: string, choose: boolean) => {
    let copySides: ISideModify[] = [];
    sides.forEach((item) => copySides.push({ ...item }));
    copySides.forEach((item) => {
      if (item.name === title) {
        item.isChoose = choose;
      }
    });
    setSides(copySides);
  };

  const clickIng = (title: string, status: boolean): void => {
    let copyDops: IDops[] = [];
    copyDops = dops.map((el) => {
      if (el.title === title) return { ...el, isSelect: status };
      return { ...el };
    });
    setDops(copyDops);
  };

  const clickPurchase = () => {
    if (item) {
      const copyItem: IProduct = { ...item };
      const optionsC = { ...options };
      const sidesC: additionalOption[] = [];
      const dopsC: Array<string> = [];
      Object.keys(optionsC).forEach(
        (key) => options[key] === undefined && delete optionsC[key]
      );
      sides.forEach((el) => el.isChoose && sidesC.push({ ...el }));
      dops.forEach((el) => el.isSelect && dopsC.push(el.title));
      copyItem.options = optionsC;
      copyItem.sides = sidesC;
      copyItem.productOption = dopsC;
      const finnalyObject: IItemCart = {
        id: String(copyItem.id + Date.now()),
        item: copyItem,
        counts: 1,
      };
      dispatch(cartActions.Actions.addInCart(finnalyObject));
      dispatch(cartActions.Actions.changeStep("cart"));
    }
  };
  useEffect(() => {
    setItem(currentProduct);
    if (currentProduct) {
      setOptions({
        size: currentProduct.options["size"],
        dough: currentProduct.options["dough"],
        volume: currentProduct.options["volume"],
      });
      setInitialPrice(currentProduct.price);
      setDops(setDopsFunc);
      setSides(setSidesFunc);
    }
  }, [currentProduct, setDopsFunc, setSidesFunc]);

  useEffect(() => {
    changePrice();
  }, [options, sides]);
  return (
    <div className="productModal">
      <ProductLeft url={!!item ? item.img : ""} />
      {!!item && (
        <ProductRight
          changeSides={changeSide}
          sides={sides}
          ingDop={clickIng}
          dops={dops}
          item={item}
          options={options}
          switcherItemEvent={changeOption}
          subTitle={setTitle()}
          clickButton={clickPurchase}
        />
      )}
    </div>
  );
};

export default Product;
