import { IPromItem, UserModel } from "../../api/models";
import { IOrdered } from "./../../api/models/orderedSlider.model";
import * as appActions from "./app.actions";
import { IProduct } from "./../../api/models/product.model";

export interface IAppReducer {
  modal: string;
  country: string;
  promotions: IPromItem[];
  ordered: IOrdered[];
  user: UserModel | null;
  product: Array<IProduct>;
  currentProduct: IProduct | null;
  openCartRedirect: boolean;
  pickupCity: Array<string>;
}

const initialState: IAppReducer = {
  modal: "",
  country: "Москва",
  promotions: [],
  ordered: [],
  user: null,
  product: [],
  currentProduct: null,
  openCartRedirect: false,
  pickupCity: [],
};

export const appReducer = (
  state = initialState,
  action: appActions.Actions
): IAppReducer => {
  switch (action.type) {
    case appActions.OPEN_MODAL:
      return {
        ...state,
        modal: action.payload,
      };
    case appActions.CLOSE_MODAL:
      return {
        ...state,
        modal: "",
      };
    case appActions.CHANGE_COUNTRY:
      return {
        ...state,
        country: action.payload,
      };
    case appActions.ADD_PROMOTIONS:
      return {
        ...state,
        promotions: action.payload,
      };
    case appActions.ADD_ORDERED:
      return {
        ...state,
        ordered: action.payload,
      };
    case appActions.ADD_CURRENTUSER:
      return {
        ...state,
        user: action.payload,
      };
    case appActions.CLEAR_CURRENTUSER:
      return {
        ...state,
        user: null,
      };
    case appActions.ADD_PIZZA:
      return {
        ...state,
        product: action.payload,
      };
    case appActions.CURRENT_PRODUCT:
      return {
        ...state,
        currentProduct: action.payload,
      };
    case appActions.FETCH_CITY_PICKUP:
      return {
        ...state,
        pickupCity: action.payload,
      };

    default:
      return state;
  }
};
